import { Component, OnInit } from '@angular/core';
import { AnalyticsService } from './@core/utils/analytics.service';
import { SeoService } from './@core/utils/seo.service';
import { Title, Meta } from '@angular/platform-browser';
import { Router, NavigationEnd } from '@angular/router';
declare let gtag;

@Component({
  selector: 'ngx-app',
  template: '<router-outlet></router-outlet>',
})
export class AppComponent implements OnInit {
  title = 'COVID-19 Global Live Data';

  constructor(
    private analytics: AnalyticsService,
    private seoService: SeoService,
    private titleService: Title,
    private metaService: Meta,
    private _router: Router
  ) {
    this._router.events.subscribe((event) => {
      if (event instanceof NavigationEnd) {
        gtag('config', 'UA-161721170-1', {
          page_path: event.urlAfterRedirects,
        });
      }
    });
  }

  ngOnInit(): void {
    this.analytics.trackPageViews();

    this.seoService.trackCanonicalChanges();
    this.titleService.setTitle(this.title);
    this.metaService.addTags([
      {
        name: 'keywords',
        content:
          'COVID-19, Covid-19 Top Headlines, Covid-19 Popular News, Covid-19 Top Countries, Covid-19 Top States, Covid-19 Data, Corona Data, Covid-19 Pie Chart, Covid-19 Bar Chart, Covid-19 Line Chart, Covid-19 Heatmaps, Covid-19 World Map, Covid-19 Maps, Covid-19 News, Covid-19 India Map, Covid-19 US Map, Covid-19 Cases, Covid-19 Confirmed Cases, Covid-19 Recovered Cases, Covid-19 Deaths, Covid now, Covid-19 now, Covid-19 today, Latest Covid-19 data, Flatten the curve, Covid-19 Historical data, Covid-19 trends, Corona, Corona Virus, COVID-19 Global Live Data, COVID-19 Tracker, Best COVID-19 Tracker, COVID-19 Dashboard, Best COVID-19 Dashboard, COVID-19 Data visualization, Corona Virus Data visualization, Corona Virus Tracker, Corona Virus Dashboard, City wise Covid, Country wise Covid, City wise Corona, Country wise Corona, Novel Corona, Global Covid Data, Global Novel Corona Data, World Covid Data, World Novel Corona Data, USA Covid Data, USA Novel Corona Data, India Covid Data, India Novel Corona Data,  UK Covid Data, UK Novel Corona Data, Italy Covid Data, Italy Novel Corona Data, Corona Pandemic, Novel Corona Pandemic, Covid-19 Pandemic, John Hopkins University Covid-19 data, John Hopkins University Corona Virus data',
      },
      { name: 'description', content: 'COVID-19 Global Live Data' },
      { name: 'robots', content: 'index, follow' },
    ]);
  }
}
